.card {
    width: 250px;
    height: 300px;
    border-radius: 20px;
    overflow: hidden;
    padding: 8px;
    position: relative;
    margin-top: 40px;
    margin-left: 3%;
    margin-right: 3%;
    border: 2px solid white;
    border-radius: 6px;
    backdrop-filter: blur(5px); 
    background-color: white;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  
  }
  
  @media (max-width: 480px) {
    .card {
      width: 100%;
      margin-left: 0;
      margin-right: 0;
    }
  }
  
  .image {
    width: 70%;
    height: 70%;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .cardbody {
    width: 100%;
    height: 100%;
    top: 0;
    right: -150%;
    position: absolute;
    background: none;
    font-size: 14px;
     backdrop-filter: blur(5px); 
    border-radius: 15px;
    background-color: white;
  
    color: rgb(20, 20, 20);
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: 1s;
    margin-top: -60px;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
    text-align: center;
  
  }
  .textd{
    text-decoration: none;
    
  
  }
  .card:hover .cardbody {
    right: 0;
  }
  
  .title {
    text-transform: uppercase;
    font-size: 15px;
    color: rgb(9, 8, 8);
    margin-top: 10px;
  }
  
  .cardsubtitle {
    text-transform: capitalize;
    font-size: 20px;
    font-weight: 300;
    display: flex;
    justify-content: center;
    margin-left: 26%;
    font-weight: 500;
  
  }
  
  .cardinfo {
    font-size: 20px;
    text-decoration: solid;
    line-height: 25px;
    margin: 0;
    font-weight: 500;
    display: flex;
    justify-content: center;
    flex-direction: column;
  
  }
  
  .cardbtn {
    background: #8fabba;
    padding: 10px 20px;
    width: 120px;
    border-radius: 5px;
    text-transform: capitalize;
    outline: none;
    font-weight: 600;
    cursor: pointer;
    backdrop-filter: blur(1px);
    transition: all 0.1s ease-in-out;
    height: 35px;
    color: rgb(1, 1, 1);
    background: none;
    display: flex;
    margin-left: 43%;
    border: 2px solid white;
    border-radius: 6px;
    margin-top: 10px;
  }
  
  .cardbtn:hover {
    cursor: pointer;
    transform: scale(1.01);
    border-bottom: 2px solid white;
    padding-top: 5px;
  }